footer {
  height: 1.6rem;
  width: 100vw;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 0.01333333rem solid #5d5d5d;
}

footer > a {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #000;
}

footer > a > span {
  font-size: 0.4rem;
  line-height: 0.66666667rem;
}

footer > a.active {
  color: coral;
  font-weight: bold;
}

.anticon {
  font-size: 0.66666667rem;
}